<template>
	<view class="cardBox">
		<view class="top">
			<view class="img-top">
				<view class="img-left">
					<view class="img-number"><view class="iconfont icon-tupian1 tupian"></view> {{number}}</view>
					<view class="img-zou"><view class="iconfont icon-genwozou genwozou"></view> 擦肩而过1次</view>
				</view>
				<view class="img-right">
					<view class="iconfont icon-v"></view>
				</view>
			</view>
			<!-- 喜欢 -->
			<view class="love" :animation="loveAnimation[0]">
				<view class="iconfont icon-xinaixin" :style="{fontSize:'60rpx'}"></view>
			</view>
			<!-- 不喜欢 -->
			<view class="loathe" :animation="loatheAnimation[0]">
				<view class="iconfont icon-chacha1" :style="{fontSize:'60rpx'}"></view>
			</view>
			<view class="suoerLove " :animation="superLove[0]">
				<image src="/static/img/super.png" mode="aspectFit"></image>
			</view>
			<!-- 图片 -->
			<image class="img" :src="src"></image>
		</view>
		<!-- 底部 -->
		<view class="bottom">
			<view class="star">
				<view class="iconfont icon-wujiaoxing1" :style="{fontSize:'60rpx'}"></view>
			</view>
			<view>{{name}}</view>
			<view class="labelBox">
				<view class="label" :style="{backgroundColor:sex==0?'#7BD8FF':'#F3C9F5'}">
					<view class="iconfont sex" :class="sex==0?'icon-male':'icon-xingbie-nv'" :style="{fontSize:'16px' }"></view> {{old}}</view>
				<view class="label" :style="{backgroundColor:'#A4C742'}">{{constellation}}</view>
			</view>
			<view class="address">{{address}}</view>
		</view>
	</view>
</template>

<script>
	export default{ 
		props:{
			src:{
				type:String,
				default:''
			},
			number:{
				type:Number,
				default:0
			},
			name:{
				type:String,
				default:''
			},
			sex:{
				type:Number,
				default:0
			},
			constellation:{
				type:String,
				default:''
			},
			address:{
				type:String,
				default:''
			},
			old:{
				type:Number,
				default:18
			}
		},
		data(){
			return{
				loveAnimation:{0:{}},
				loatheAnimation:{0:{}},
				superLove:{0:{}},
				loveAni:null,
				loatheAni:null,
				super:null
			}
		},
		mounted() {
			//touch移动喜欢动画
			this.loveAni = uni.createAnimation({
				duration:0
			});
			//touch移动不喜欢动画
			this.loatheAni = uni.createAnimation({
				duration:0
			});
			this.super = uni.createAnimation({
				duration:0
			});
		},
		methods:{
			clearAnimation(){
				this.loveAnimation[0] = this.loveAni.export()
				this.loatheAnimation[0] = this.loatheAni.export()
				this.superLove[0] = this.super.export()
			},
			_back(){
				//touch移动喜欢动画
				this.loveAni = uni.createAnimation({
					duration:200
				});
				//touch移动不喜欢动画
				this.loatheAni = uni.createAnimation({
					duration:200
				});
				this.loveAni.opacity( 0 ).step()
				this.loveAnimation[0] = this.loveAni.export()
				this.loatheAni.opacity( 0 ).step()
				this.loatheAnimation[0] = this.loatheAni.export()
				this.super.opacity( 0 ).step()
				this.superLove[0] = this.super.export()
			},
			moveRight(ratio){
				this.super.opacity( 0 ).step()
				this.superLove[0] = this.super.export()
				this.loveAni.opacity( 0.3 + 0.7*ratio ).step()
				this.loveAnimation[0] = this.loveAni.export()
				this.loatheAni.opacity( 0 ).step()
				this.loatheAnimation[0] = this.loatheAni.export()
			},
			moveLeft(ratio){
				this.super.opacity( 0 ).step()
				this.superLove[0] = this.super.export()
				this.loveAni.opacity( 0 ).step()
				this.loveAnimation[0] = this.loveAni.export()
				this.loatheAni.opacity( 0.3 + 0.7*ratio ).step()
				this.loatheAnimation[0] = this.loatheAni.export()
			},
			moveTop(ratio){
				this.super.opacity( 0 ).step()
				this.superLove[0] = this.super.export()
				this.super.opacity( 0.3 + 0.7*ratio/4 ).step()
				this.superLove[0] = this.super.export()
			},
			moveCenter(){
				this.loveAni.opacity( 0.3 ).step()
				this.loveAnimation[0] = this.loveAni.export()
				this.loatheAni.opacity( 0.3 ).step()
				this.loatheAnimation[0] = this.loatheAni.export()
			}
		},
		watch:{
			cardData(newVal){
				console.log(newVal)
				this.item = newVal
			}
		}
	}
</script>

